.example {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.5;
  background-color: #fff;
  color: #424242;
  max-width: 960px;
  padding: 16px;
}

.example code {
  font-family: monospace;
  font-size: 16px;
  font-weight: 700;
  background-color: #009688;
  color: #fff;
  padding: 2px 6px;
  border-radius: 2px;
}

.example .background {
  height: 240px;
  // Путь к изображениям из SASS-файла всегда такой, как из папки css
  background-image: url('../images/demo.jpg');
  background-size: cover;
  background-position: center;
}

.search-form {
  text-align: center;
  background-color: rgb(107, 69, 242);
  padding: 20px;
  input {
    border-radius: 4px;
    border: none;
    height: 32px;
  }
  button {
    height: 32px;
    width: 32px;
    border-radius: 4px;
    border: none;
  }
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  padding: 20px;
  margin-left: 10px;
}

.photo-card {
  width: 300px;
  border: 1px solid black;
  border-radius: 3px;
  margin-top: 10px;
  margin-left: 10px;
  img {
    width: 300px;
    height: 200px;
  }
  .info {
    display: flex;
    // text-align: center;
    // b {
    //   margin-bottom: 10px;
    // }
  }
}

.load-more {
  display: none;
  text-align: center;
}

